	},
	onShow() {

		uni.getStorage({
			key: 'delAddress',
			success: (e) => {
				let len = this.addressList.length;
				if (e.data.hasOwnProperty('id')) {
					for (let i = 0; i < len; i++) {
						if (this.addressList[i].id == e.data.id) {
							this.addressList.splice(i, 1);
							break;
						}
					}
				}
				uni.removeStorage({
					key: 'delAddress'
				})
			}
		})
		uni.getStorage({
			key: 'saveAddress',
			success: (e) => {
				let len = this.addressList.length;
				if (e.data.hasOwnProperty('id')) {
					for (let i = 0; i < len; i++) {
						if (this.addressList[i].id == e.data.id) {
							this.addressList.splice(i, 1, e.data);
							break;
						}
					}
				} else {
					let lastid = this.addressList[len - 1];
					lastid++;
					e.data.id = lastid;
					this.addressList.push(e.data);
				}
				uni.removeStorage({
					key: 'saveAddress'
				})
			}
		})
	},
	onLoad(e) {
		if (e.type == 'select') {
			this.isSelect = true;
		}
	},
