				cancelColor:'rgba(102,102,102,1)',
				confirmColor:'rgba(20,204,33,1)',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定')
						
						uni.showToast({
							icon: "success",
							title: '操作成功!',
							duration: 2000
						});
					} else if (res.cancel) {
						console.log('用户点击取消')
					}
				}
			});
		},
		switchType(type) {

			this.typeClass = type;
			if (type == "home") {
				this.subState = 1;

			} else if (type == "self") {
				this.subState = 2;
			}
		},
		edit(row) {
			uni.setStorage({
				key: 'address',
				data: row,
				success() {
					uni.navigateTo({
						url: "/pages/user/address/edit/edit?type=edit"
					})
				}
			});

		},
		clear(row) {
			console.log(row)
		},
		add() {
			uni.navigateTo({
				url: "edit/edit?type=add"
			})
		},
		select(row) {
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if (!this.isSelect) {
				return;
			}
			uni.setStorage({
				key: 'selectAddress',
				data: row,
				success() {
					uni.navigateBack();
				}
			})
		}
	}
